:root {
  --white: #F8F9FA;
  --black: #000014;
  --grey: #f4f4f4;
  --accent: #F8EAC9;
}

:root {
  --font-norms: "Poppins", sans-serif;
}

body {
  font-family: var(--font-norms);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: var(--font-norms);
}

ul {
  list-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

textarea {
  overflow: auto;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.main__wrapper {
  width: 100%;
}

.main__page {
  margin-left: 18.75rem;
}

@media screen and (max-width: 1024px) {
  .main__page {
    margin-left: 0;
  }
}
html {
  scroll-behavior: smooth;
}

.sidenav {
  position: fixed;
  display: flex;
  flex-direction: column;
  row-gap: 1.125rem;
  width: 18.75rem;
  height: 100%;
  padding: 5rem 0 0 1.5rem;
  z-index: 1;
  top: 0;
  left: 0;
}

.instagram__link {
  margin-top: 1.5rem;
}

.logo {
  width: 7rem;
}

.sidenav__item {
  font-size: 1.125rem;
}

.sidenav__item a {
  padding: 0.25rem;
  color: var(--black);
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: all 0.55s ease;
}

.sidenav__item a:hover {
  border-bottom: 1px solid #ADB5BD;
}

.menuToggle input {
  display: none;
}

@media screen and (max-width: 1024px) {
  .header__main {
    background-color: #fff;
    width: 100%;
    height: 80px;
  }
  .menuToggle {
    display: block;
    position: absolute;
    top: 30px;
    right: 50px;
    z-index: 1;
    -webkit-user-select: none;
    user-select: none;
  }
  .sidenav {
    transition: 0.5s;
    width: 18.75rem;
    height: auto;
    margin: -100px 0 0 0;
    padding: 50px;
    padding-top: 155px;
    left: auto;
    right: 0;
    background: var(--white);
    list-style-type: none;
    -webkit-font-smoothing: antialiased;
    /* to stop flickering of text in safari */
    transform-origin: 0% 0%;
    transform: translate(100%, 0);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  }
  .menuToggle input {
    display: block;
    width: 40px;
    height: 32px;
    position: absolute;
    top: -7px;
    left: -5px;
    cursor: pointer;
    opacity: 0;
    /* hide this */
    z-index: 3;
    /* and place it over the hamburger */
    -webkit-touch-callout: none;
  }
  .menuToggle span {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background: #cdcdcd;
    border-radius: 3px;
    z-index: 2;
    transform-origin: 4px 0px;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
  }
  .menuToggle span:first-child {
    transform-origin: 0% 0%;
  }
  .menuToggle span:nth-last-child(2) {
    transform-origin: 0% 100%;
  }
  .menuToggle input:checked ~ span {
    opacity: 1;
    transform: rotate(45deg) translate(-2px, -1px);
    background: #232323;
  }
  .menuToggle input:checked ~ span:nth-last-child(3) {
    opacity: 0;
    transform: rotate(0deg) scale(0.2, 0.2);
  }
  .menuToggle input:checked ~ span:nth-last-child(2) {
    transform: rotate(-45deg) translate(0, -1px);
  }
  .menuToggle input:checked ~ ul {
    transform: none;
  }
  .logo {
    position: relative;
    display: block;
    top: 20px;
    left: 20px;
  }
}
.about__container {
  display: flex;
  justify-content: space-between;
  background: var(--grey);
}

.about__description {
  margin: 6.25rem 0 0 1.5rem;
  max-width: 23.75rem;
}

.about__title {
  font-size: 3.375rem;
  font-weight: 700;
}

.about__text {
  font-size: 1rem;
  line-height: 1.5rem;
}

.about__photo {
  max-width: 38.75rem;
  max-height: 50rem;
  margin-right: 1.5rem;
}

.about__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 768px) {
  .about__container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about__description {
    max-width: 100%;
    margin: 6.25rem 0 1.5rem 1.5rem;
  }
  .about__photo {
    margin-right: 0;
    margin-bottom: 1.5rem;
    padding: 0 0.75rem;
  }
  .about__img {
    max-height: 50rem;
  }
}
.faq {
  margin-top: 3.75rem;
}

.faq__container {
  background: var(--grey);
}

.faq__answers {
  padding-bottom: 3.75rem;
}

.faq__list {
  padding: 0;
}

.faq__title {
  margin: 0;
  padding: 6.25rem 0 0 1.5rem;
  font-size: 3.375rem;
}

.faq__collapsible {
  position: relative;
  background-color: var(--grey);
  color: var(--black);
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 1rem;
}

.faq__collapsible::after {
  content: "";
  display: block;
  background: url("/assets/tattoo-machine.png") no-repeat;
  width: 24px;
  height: 24px;
  float: right;
  margin: 0 0.375rem 0 0;
}

.faq__active,
.faq__collapsible:hover {
  background-color: var(--accent);
}

@-webkit-keyframes faq__active {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}
@-moz-keyframes faq__active {
  0% {
    opacity: 0;
    -moz-transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    -moz-transform: translateY(0);
  }
}
@-o-keyframes faq__active {
  0% {
    opacity: 0;
    -o-transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    -o-transform: translateY(0);
  }
}
@keyframes faq__active {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.faq__active {
  -webkit-animation: fadeInDown 1s both 0.5s;
  -moz-animation: fadeInDown 1s both 0.5s;
  -o-animation: fadeInDown 1s both 0.5s;
  animation: fadeInDown 1s both 0.5s;
}

.faq__content {
  padding: 0 18px;
  display: none;
  overflow: hidden;
  background-color: #f1f1f1;
  border-bottom: 1px solid var(--black);
}

.faq__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 25px;
  grid-row-gap: 25px;
}

.faq__content ul {
  padding-top: 0.75rem;
  padding-left: 1.125rem;
  list-style: disc;
}

.faq__content li {
  padding-bottom: 0.75rem;
}

@media screen and (max-width: 768px) {
  .faq__list {
    grid-template-columns: repeat(1, 1fr);
  }
}
.contact {
  margin-top: 3.75rem;
}

.contact__title {
  margin-left: 1.5rem;
  font-size: 2.625rem;
}

.tatoo__form {
  padding: 1.5rem;
}

.tatoo__label {
  font-size: 1.125rem;
  font-weight: 600;
}

.tatoo__label span {
  color: #989ba5;
  font-size: 0.75rem;
}

.tatoo__label-sub {
  font-size: 0.875rem;
  font-weight: 500;
}

.tatoo__input {
  width: 100%;
  margin-top: 0.875rem;
  margin-bottom: 1.5rem;
  padding: 0.5rem 0.75rem;
  border: 1px solid #cccccc;
  border-radius: 5px;
}

.tatoo__name {
  display: flex;
  justify-content: space-between;
  margin-top: 1.125rem;
}

.tatoo__first {
  width: 50%;
  margin-right: 1.5rem;
}

.tatoo__last {
  width: 50%;
}

.tatoo__checkbox_wrap {
  display: block;
  margin-top: 0.875rem;
  margin-bottom: 1.5rem;
  cursor: pointer;
}

.tatoo__checkbox {
  margin-right: 0.5rem;
}

.tatoo__message {
  display: block;
  margin-top: 0.875rem;
  margin-bottom: 1.5rem;
  padding: 0.5rem 0.75rem;
  border: 1px solid #cccccc;
  border-radius: 5px;
  resize: none;
}

.tatoo__wrap {
  margin: 1.5rem 0;
}

.tatoo__wrap .tatoo__checkbox {
  margin-left: 0.75rem;
}

.tatoo__submit {
  padding: 0.625rem 1rem;
  color: var(--black);
  border: 1px solid var(--black);
  background-color: var(--white);
  border-radius: 5px;
  cursor: pointer;
}

.tatoo__submit:hover {
  color: var(--white);
  background-color: var(--black);
}

.file-label {
  display: block;
  background-color: var(--white);
  padding: 0.625rem 1rem;
  border: 1px solid var(--black);
  border-radius: 5px;
  color: var(--black);
  height: auto;
  max-width: 7.5rem;
  margin-top: 1.5rem;
  text-align: center;
  font-size: 0.875rem;
  cursor: pointer;
}

.file-label:hover,
.file-label:hover {
  color: var(--white);
  background-color: var(--black);
}

.file-label:active,
.file-label:active {
  color: var(--white);
  background-color: var(--black);
}

.tatoo__form ol {
  padding-left: 0;
}

.tatoo__form img {
  height: 64px;
  order: 1;
}

.tatoo__form li {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  list-style-type: none;
}

@media screen and (max-width: 768px) {
  .tatoo__message {
    max-width: 300px;
  }
  .tatoo__form li {
    flex-direction: column;
  }
  .tatoo__form img {
    max-width: 20rem;
  }
}
.instagram {
  margin-top: 3.75rem;
}